* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Arial, Helvetica, sans-serif;
}

body {
    background: #f1f5f8;
    color: #222;
}

.booklist {
    width: 90vw;
    max-width: 1170px;
    margin: 5rem auto;
    display: grid;
    gap: 2rem;
}

@media screen and (min-width: 768px) {
    .booklist {
        grid-template-columns: repeat(3, 1fr);
    }
}

.book {
    height: 280px;
    background: #fff;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    position: relative;
}

.book img {
    width: 100%;
    height: 150px;
    object-fit: contain;
}

.book h2 {
    margin-top: 1rem;
    font-size: 1.1rem;
}

.book h4 {
    color: #617d98;
    font-size: 0.75rem;
    margin-top: 0.5rem;
    letter-spacing: 2px;
}

.header {
    position: absolute;
    top: 25px;
    right: 650px;
}

.header-1 {
    display: flex;
    align-items: center;
    height: 50px;
}

.index-num {
    position: absolute;
    top: 0px;
    left: 0px;
    padding: 10px;
    border-radius: 10px 0 10px 0;
    background-color: red;
}